/* eslint-disable no-constant-condition */
/* eslint-disable getter-return */
/* eslint-disable no-param-reassign */
import urlParse from '../script/routes/urlParse';
import { cardTemplate, listTemplate } from '../templates/templateCreator';

class MoviesOrTv extends HTMLElement {
  set title(title) {
    this._title = title;
    // console.log(this._title);
  }

  set layout(layout) {
    this._layout = layout;
    // console.log(this._layout);
  }

  set totalData(totalData) {
    this._totalData = totalData;
    // console.log(this._totalData);
  }

  set pagination(pagination) {
    this._pagination = pagination;
    // console.log(this._pagination)
  }

  set link(link) {
    this._link = link;
    // console.log(this._link);
  }

  set slug(slug) {
    this._slug = slug;
    // console.log(this._slug);
  }

  set numberPage(page) {
    this._page = page;
    // console.log(page);
  }

  set data(data) {
    this._data = data;
    this.render();
    this.afterRender();
    // console.log(this._data);
  }

  set next(event) {
    this._next = event;
    this.render();
    this.afterRender();
    // this.setTitle();
    // console.log(this._next);
  }

  set previous(event) {
    this._previous = event;
    this.render();
    this.afterRender();
    // this.setTitle();
    // console.log(this._previous);
  }

  buttonPagination() {
    this.querySelector('.number-of-page').value = this._page;
    if (this._page === 1) {
      this.querySelector('.prev').setAttribute('disabled', true);
      this.querySelector('.prev').classList.add('bg-red-700');
      this.querySelector('.prev').classList.add('text-slate-400');
    }
    this.querySelector('.prev').addEventListener('click', this._previous);
    this.querySelector('.next').addEventListener('click', this._next);
  }

  render() {
    const url = urlParse.parseUrlWithoutCombiner();
    this.innerHTML = listTemplate(this._title, url.pages, this._layout, this._pagination, this._link, this._slug);

    // set width scroll container
    if (this._layout === 'flex') {
      this.querySelector('.list-card').style.width = `${158 * `${this._totalData ? this._totalData : this._data.length}`}px`;
    }

    if (this._pagination) {
      this.buttonPagination();
    }
  }

  afterRender() {
    const container = this.querySelector('.list-card');
    const url = urlParse.parseUrlWithoutCombiner();

    // console.log(this._data);

    this._data.slice(0, this._totalData).forEach((movie) => {
      container.innerHTML += cardTemplate(movie, url.pages, this._layout);
    });

    // if (this._data.latest) {
    //   container.innerHTML = latestTemplate(this._data);
    // }
  }
}

customElements.define('list-tag', MoviesOrTv);
